.modal {
  position: fixed;
  color: #000000;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
  background-color: #ffffff;
  top: 120px;
  left: 50%;
  padding: 43px 20px 52px 20px;
  transform: translateX(-50%);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  z-index: 21;
}

.modal__error {
  display: none;
}

.modal__success {
  display: none;

  background-color: #ffffff;

  & .modal__description {
    padding-top: 35px;
    padding-bottom: 38px;

  }
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 20;
  display: none;

}

.modal__close {
  width: 280px;
  margin: 0 auto;
  height: 64px;
  cursor: pointer;
  font: inherit;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 700;

  border-radius: 3px;
  border: none;
  background-color: #2aab6d;
  color: #ffffff;

  &:hover {
    background-color: #1c9b5e;
  }

  &:active {
    background-color: #1c9b5e;
    color: #5cb78b;
  }
}

.modal__title {
  margin: 0 0 40px 0;
  padding: 0 20px 0 20px;

  font-size: 18px;
  line-height: 24px;
}

.modal__description {
  margin: 0 0 45px 0;

  font-size: 14px;
  line-height: 24px;
}

.modal__show {
  display: block;
}

@media (min-width: @tablet-width) {
  .modal {
    width: 460px;
    padding: 43px 75px 52px 75px;
  }

  .modal__success .modal__description {
    padding-top: 35px;
    padding-bottom: 38px;
  }

  .modal__close {
    width: 300px;
    height: 64px;
    font-size: 18px;
  }

  .modal__title {
    margin: 0 0 40px 0;
    padding: 0 20px 0 20px;

    font-size: 30px;
    line-height: 40px;
  }

  .modal__description {
    margin: 0 0 45px 0;

    font-size: 18px;
    line-height: 24px;
  }
}

@media (min-width: @desktop-width) {
  .modal {
    width: 782px;
    padding: 50px 210px 50px 210px;
  }

  .modal__success .modal__description {
    padding-top: 35px;
    padding-bottom: 38px;
  }

  .modal__close {
    width: 300px;
    height: 64px;
    font-size: 18px;
  }

  .modal__title {
    margin: 0 0 40px 0;
    padding: 0 20px 0 20px;

    font-size: 30px;
    line-height: 40px;
  }

  .modal__description {
    margin: 0 0 45px 0;

    font-size: 18px;
    line-height: 24px;
  }
}
