    justify-content: space-between;
    width: 622px;
    margin-left: 448px;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    height: 3px;
    background: #ffffff;
  }
  .site-list__item a {
    margin: 0;
    padding: 0;
    padding-top: 45px;
    background: none;
    border: none;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;
