.main-nav {
  position: relative;
  font-size: 18px;
  line-height: 32px;
  color: #ffffff;
  text-align: center;
  background-color: rgba(29, 38, 50, 0.61);
  min-width: 320px;
  min-height: 70px;
  margin: 0;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 90px;
  height: 70px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  left: 20px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.33);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.33), 0 20px 0 0 rgba(210, 40, 86, 0.33);
}
.main-nav--opened {
  background-color: #283645;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  left: 45px;
  width: 30px;
  height: 5px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1199px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 660px) {
  .main-nav {
    min-height: 109px;
    min-width: 660px;
  }
  .main-nav__toggle {
    height: 109px;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    top: 54px;
  }
  .main-nav--closed .main-nav__toggle::before,
  .main-nav--closed .main-nav__toggle::after {
    top: 44px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
