.page-footer {
  background-color: #ffffff;
  color: #283645;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.page-footer__wrapper {
  margin: 0 auto;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  background-color: white;
}
.page-footer__logo {
  margin: 38px auto;
  margin-bottom: 30px;
}
.page-footer__logo-image {
  fill: black;
  width: 150px;
  height: auto;
}
.page-footer__logo-glasses {
  fill: #d22856;
  width: 200px;
  height: auto;
}
.page-footer__logo .page-footer__logo-image:active {
  opacity: 0.3;
  fill: black;
}
.page-footer__logo-image:hover {
  fill: #d22856;
}
.page-footer__logo-desktop {
  display: none;
}
.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  min-width: 180px;
  max-width: 340px;
  margin: 0 auto;
  justify-content: center;
}
.social__item {
  margin: 0 6px;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 45px;
  font-size: 0;
  border: 3px solid #e9ebec;
  border-radius: 50%;
}
.social__link:hover {
  border: 3px solid black;
}
.page-footer__copyright {
  margin: 46px auto;
  height: 20px;
  display: flex;
  align-items: center;
}
.copyright__link {
  display: flex;
  align-items: center;
}
.copyright__link:hover svg {
  fill: #000000;
}
.copyright__link:active svg {
  fill: #bec2c7;
}
.copyright__link:active {
  color: black;
}
.copyright__text {
  display: inline-block;
  padding-right: 8px;
  padding-left: 5px;
}
.page-footer__htmlacademy {
  fill: #d22856;
  width: 40px;
  height: auto;
}
.page-footer__index {
  display: none;
}
@media (min-width: 660px) {
  .page-footer__index {
    display: block;
  }
  .page-footer__wrapper {
    min-width: 660px;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 39px;
  }
  .page-footer__logo {
    margin-left: 18px;
    margin-right: 0;
    margin-top: 0;
  }
  .social__list {
    margin-top: 6px;
    padding-right: 15px;
  }
  .page-footer__copyright {
    margin-left: 0;
    margin-right: 16px;
    margin-top: 22px;
  }
}
@media (min-width: 1200px) {
  .page-footer__wrapper {
    min-width: 1200px;
    padding-top: 95px;
  }
  .page-footer__logo-glasses,
  .page-footer__logo-text {
    display: none;
  }
  .page-footer__logo-desktop {
    display: block;
  }
  .page-footer__logo {
    margin-left: 129px;
  }
  .social__list {
    padding-right: 0;
    padding-left: 18px;
  }
  .page-footer__copyright {
    margin-left: 0;
    margin-right: 123px;
    margin-top: 22px;
  }
  .copyright__text {
    font-size: 18px;
  }
}
.modal {
  position: fixed;
  color: #000000;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
  background-color: #ffffff;
  top: 120px;
  left: 50%;
  padding: 43px 20px 52px 20px;
  transform: translateX(-50%);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  z-index: 21;
}
.modal__error {
  display: none;
}
.modal__success {
  display: none;
  background-color: #ffffff;
}
.modal__success .modal__description {
  padding-top: 35px;
  padding-bottom: 38px;
}
.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 20;
  display: none;
}
.modal__close {
  width: 280px;
  margin: 0 auto;
  height: 64px;
  cursor: pointer;
  font: inherit;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 3px;
  border: none;
  background-color: #2aab6d;
  color: #ffffff;
}
.modal__close:hover {
  background-color: #1c9b5e;
}
.modal__close:active {
  background-color: #1c9b5e;
  color: #5cb78b;
}
.modal__title {
  margin: 0 0 40px 0;
  padding: 0 20px 0 20px;
  font-size: 18px;
  line-height: 24px;
}
.modal__description {
  margin: 0 0 45px 0;
  font-size: 14px;
  line-height: 24px;
}
.modal__show {
  display: block;
}
@media (min-width: 660px) {
  .modal {
    width: 460px;
    padding: 43px 75px 52px 75px;
  }
  .modal__success .modal__description {
    padding-top: 35px;
    padding-bottom: 38px;
  }
  .modal__close {
    width: 300px;
    height: 64px;
    font-size: 18px;
  }
  .modal__title {
    margin: 0 0 40px 0;
    padding: 0 20px 0 20px;
    font-size: 30px;
    line-height: 40px;
  }
  .modal__description {
    margin: 0 0 45px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .modal {
    width: 782px;
    padding: 50px 210px 50px 210px;
  }
  .modal__success .modal__description {
    padding-top: 35px;
    padding-bottom: 38px;
  }
  .modal__close {
    width: 300px;
    height: 64px;
    font-size: 18px;
  }
  .modal__title {
    margin: 0 0 40px 0;
    padding: 0 20px 0 20px;
    font-size: 30px;
    line-height: 40px;
  }
  .modal__description {
    margin: 0 0 45px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
